body {
  	margin: 0;
 	padding: 0;
	background-image:url(/demographic-app/static/media/bluewave.2a50ada7.png);
	background-repeat:no-repeat;
	background-size:cover;
	background-attachment: fixed;
	font-family:helvetica,sans-serif;
}


.App {
  text-align: center;
  display:flex-column;
  -webkit-justify-content: center;
          justify-content: center;
}

.imageForm{
	width:700px;
}

button{
	cursor:pointer;
}

.box{
	position:absolute;
	box-shadow:0 0 0 3px #149df2 inset;
	display:-webkit-flex;
	display:flex;
	-webkit-flex-wrap:wrap;
	        flex-wrap:wrap;
	-webkit-justify-content: center;
	        justify-content: center;
	cursor:pointer;
}

.center{
	display:-webkit-flex;
	display:flex;
	-webkit-justify-content: center;
	        justify-content: center;

}


input[type=file]::-webkit-file-upload-button {
  color:gray;
  padding:9px;
  border:1px solid white;
}

.submitBtn{
	background:white;
	color:navy;
	border:solid 2px navy;

}
.submitBtn:hover{
	color:white;
	background:navy;
}
input:focus{
	outline:none;
}
button:focus{
	outline:none;
}
#inputImage{
	width:600px;
	height:auto;
}

